import React, { FC } from 'react';

import Notification from '../../../components/notification/notification';

type Props = {
  isOffline?: boolean;
  isSlow?: boolean;
  hasTimeout?: boolean;
  isError?: boolean;
};

const NetworkStatus: FC<Props> = ({
  isOffline,
  isSlow,
  hasTimeout,
  isError
}: Props) => (
  <>
    {isOffline && <Notification message="Voc est offline!" />}
    {isSlow && (
      <Notification
        variation="warning"
        message="Sua conexo parece est lenta, a solicitao pode demorar!"
      />
    )}
    {hasTimeout && (
      <Notification message="A API no conseguiu responder a tempo, tente novamente!" />
    )}
    {isError && (
      <Notification message="Houve um erro na simulao, tente novamente!" />
    )}
  </>
);

export default NetworkStatus;
